<template>
  <q-card flat style="max-width: 100%;">
    <q-card-section>
      <h1>自動ポート開放</h1>
      <p>
        ルーターに対しては自動でポートを解放する機能がServerStarterに付属しています
        自動でポートの開放を行い、素早くサーバーを起動しましょう！
      </p>
      <ol>
        <li>ServerStarterを開き、システム設定を開く</li>
        <li>Networkタブを開き、「自動ポート開放を利用する」を「はい」に設定</li>
        <li>開放するポート番号を指定し、「チェック」を押す</li>
        <li>ステータスがOpened!になったら本機能を利用してルーターのポート開放を行うことが可能です</li>
        ※なお、ステータスがFailed…になった場合は、ご利用者様の環境では本機能をご利用いただけませんので、ご自分でルーターにポート設定を行っていただく必要があります
        ※Failed...になる主な理由としましては、ルーターにてUPnPを拒否する設定を行っている、多重ルーターによるネットワークになっているなどが考えられます
      </ol>
    </q-card-section>
  </q-card>
</template>